import React, { useCallback } from 'react';
import {
  Typography,
  Box,
  Grid,
  ButtonUnstyled,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import copy from 'copy-to-clipboard';

import { useStyles } from './styles';
import { TOKENS } from '../../constants/tokens';
import hole_icon from '../../assets/images/Hole.png';
import Item from './Item';
import { AddTokenParams, Token } from '../../constants/interface';
import { addTokenToMetamask, shortenAddress } from '../../utils';
import copyIcon from '../../assets/images/copy.png';

const Tokens: React.FC = () => {
  const classes = useStyles();
  const theme = useTheme();
  const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

  const onAddToken = useCallback((token: Token) => {
    const { address, decimals, symbol, icon } = token;
    const params: AddTokenParams = {
      address,
      decimals,
      symbol,
      image: icon,
    };
    addTokenToMetamask(params);
  }, []);

  const handleCopyAddress = useCallback(() => {
    copy(TOKENS['HOLE'].address);
  }, []);

  return (
    <Box className={classes.container}>
      <Box className={classes.contentTop}>
        <Grid
          container
          spacing={{ xs: 2, md: 2.5 }}
          columns={{ xs: 4, sm: 8, md: 12 }}>
          <Grid item xs={8} sm={8} md={4}>
            <Box className={classes.hamItem}>
              <Item token={TOKENS['HAM']} onAdd={onAddToken} />
            </Box>
          </Grid>
          <Grid item xs={8} sm={8} md={8}>
            <Box className={classes.holeItem}>
              <Box className={classes.holeItemTop}>
                <img src={hole_icon} width={150} height={70} alt={''} />
              </Box>
              <Box className={classes.holeItemBottom}>
                <Typography className={classes.address}>
                  {isSmall
                    ? shortenAddress(TOKENS['HOLE'].address)
                    : TOKENS['HOLE'].address}
                </Typography>
                <ButtonUnstyled
                  className={classes.copyButton}
                  onClick={handleCopyAddress}>
                  <img src={copyIcon} width={20} height={20} alt={'copy'} />
                </ButtonUnstyled>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Box>
      <Box className={classes.content}>
        <Grid
          container
          spacing={{ xs: 2, md: 2.5 }}
          columns={{ xs: 4, sm: 8, md: 12 }}>
          {Object.keys(TOKENS).map((key, index) => {
            if (key === 'HAM' || key === 'HOLE' || key === 'VIRUS' || key === 'VACCINE') {
              return null;
            }
            return (
              <Grid
                item
                xs={2}
                sm={4}
                md={4}
                key={key}
                className={classes.item}>
                <Item
                  token={TOKENS[key]}
                  onAdd={onAddToken}
                  imageSize={isSmall ? 50 : 85}
                  backgroundColor={index > 3 ? '#DCEADD' : '#FAEADD'}
                  isSmall={true}
                />
              </Grid>
            );
          })}
        </Grid>
      </Box>
    </Box>
  );
};

export default Tokens;
