import React from 'react';
import {
  Typography,
  Box,
  ButtonUnstyled,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { useStyles } from './styles';
import { useTranslation } from 'react-i18next';
import { Token } from '../../../constants/interface';
import clsx from 'clsx';

interface Props {
  token: Token;
  onAdd: (token: Token) => void;
  imageSize?: number;
  backgroundColor?: string;
  isSmall?: boolean;
}

const Item: React.FC<Props> = (props) => {
  const { token, onAdd, backgroundColor = '#FAEADD', isSmall } = props;
  const classes = useStyles();
  const [t] = useTranslation();
  const onClick = () => onAdd(token);
  const theme = useTheme();
  const mediaSmall = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Box
      className={clsx(classes.container, isSmall ? classes.smallItem : null)}
      style={{ backgroundColor: backgroundColor }}>
      <Box className={classes.item}>
        <Box className={classes.itemLeft}>
          <img
            src={require(`../../../assets/images/${token.name}.png`).default}
            alt={''}
            className={classes.itemImage}
          />
        </Box>
      </Box>
      <Box className={classes.item}>
        <Typography
          sx={{ fontSize: mediaSmall && isSmall ? 18 : 22, fontWeight: 500 }}>
          {token.name}
        </Typography>
        <ButtonUnstyled className={classes.button} onClick={onClick}>
          <Typography>{t('Add')}</Typography>
        </ButtonUnstyled>
      </Box>
    </Box>
  );
};

export default Item;
