import {
  BSCSCAN_URL,
  BSC_CHAINID,
  BSC_ENDPOINT,
  BSC_NETWORK_NAME,
} from '../constants';
import { AddTokenParams } from '../constants/interface';

export const addBscNetwork = async () => {
  const provider = (window as Window).ethereum;
  if (provider) {
    try {
      await provider.request({
        method: 'wallet_addEthereumChain',
        params: [
          {
            chainId: BSC_CHAINID
              ? `0x${parseInt(BSC_CHAINID)?.toString(16)}`
              : '0x61',
            chainName: BSC_NETWORK_NAME || 'BSC',
            nativeCurrency: {
              name: 'BNB',
              symbol: 'BNB',
              decimals: 18,
            },
            rpcUrls: [BSC_ENDPOINT],
            blockExplorerUrls: [BSCSCAN_URL],
          },
        ],
      });
      return true;
    } catch (error) {
      console.error(error);
      return false;
    }
  } else {
    console.error(
      'Can not setup the network on metamask because window.ethereum is undefined',
    );
    return false;
  }
};

export async function addTokenToMetamask(token: AddTokenParams | undefined) {
  const provider = (window as Window).ethereum;
  if (provider && provider.isMetaMask && provider.request && token) {
    await provider
      .request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20',
          options: {
            address: token.address,
            symbol: token.symbol,
            decimals: token.decimals,
            image: token.image,
          },
        },
      })
      .then((success: boolean) => {})
      .catch(() => {});
  } else {
  }
}

export function shortenAddress(address: string, chars = 4): string {
  return `${address.substring(0, chars + 2)}...${address.substring(
    42 - chars,
  )}`;
}
