import React from 'react';
import { useTranslation } from 'react-i18next';
import { Typography, Box } from '@mui/material';

import { Token } from '../../../constants/interface';
import { useStyles } from './styles';

interface Props {
  token1: Token;
  token2?: Token;
  value?: number;
}

const LargeItem: React.FC<Props> = (props) => {
  const { token1, token2, value } = props;
  const classes = useStyles();
  const [t] = useTranslation();

  return (
    <div className={classes.container}>
      <Box className={classes.iconItem}>
        <Box className={classes.imageBox}>
          <img
            src={require(`../../../assets/images/${token1.name}.png`).default}
            alt={''}
            className={classes.image}
          />
        </Box>
        <Typography>{token1.name}</Typography>
      </Box>
      {token2 && (
        <Box className={classes.orText}>
          <Typography className={classes.orText}>{t('or')}</Typography>
        </Box>
      )}
      {token2 && (
        <Box className={classes.iconItem}>
          <Box className={classes.imageBox}>
            <img
              src={require(`../../../assets/images/${token2.name}.png`).default}
              alt={''}
              className={classes.image}
            />
          </Box>
          <Typography>{token2.name}</Typography>
        </Box>
      )}
      {value && (
        <Box className={classes.rightText}>
          <Typography sx={{ fontSize: 22 }}>{`x ${value}`}</Typography>
        </Box>
      )}
    </div>
  );
};

export default LargeItem;
